import React, { useState, useEffect } from 'react';

import {Container} from '../../styles';

import api from '../../services/api';

interface Iproduct{
    id: number;
    photo: string;
    name: string;
    description: string;
    price: number;
 }

const Home: React.FC = () => {
    const [ data, setData ] = useState<Iproduct[]> ([]);
    useEffect(() => {
        api.get('').then(
            Response => {
                setData(Response.data)
            }
        )
    }, [])

    return (
        <Container>
            <section>
                Batata
                { data.map( prod => 
                    <div className="product-content" key={prod.id}>
                        <img src={prod.photo} alt="XÃ­cara preta" width="200" height="auto" />
                        <h4>{prod.name}</h4>
                        <span>{prod.description}</span>
                        <h6>{prod.price}</h6>
                    </div>
                )}
            </section>
        </Container>
    );
}

export default Home;